import {profileAPI, usersAPI} from "../api/api";

const ADD_POST = 'ADD-POST'
const SET_USER_PROFILE = 'SET_USER_PROFILE'
const SET_STATUS = 'SET_STATUS'


let initialState = {
    posts: [],
    profile: null,
    status: ''
}

const profileReducer = (state = initialState, action) => {
    switch (action.type) {  // Решил впервые попробовать через switch case. Using switch case for the first time
        case ADD_POST:
            let body = action.newPostBody
            return {
                ...state,
                posts: [...state.posts, {id: 5, text: body, likesCount: 0}]
            }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            }
        case SET_STATUS:
            return {
                ...state,
                status: action.status
            }
        default:
            return state
    }
}


export const addPostActionCreator = newPostBody => ({type: ADD_POST, newPostBody: newPostBody})
export const getUserProfileDone = profile => ({type: SET_USER_PROFILE, profile: profile})
export const setStatus = status => ({type: SET_STATUS, status: status})


export const getProfile = profile => async dispatch => {
    const res = await usersAPI.getProfile(profile)
    dispatch(getUserProfileDone(res.data))
}

export const getStatus = userId => async dispatch => {
    const res = await profileAPI.getStatus(userId)
    dispatch(setStatus(res.data))
}

export const updateStatus = status => async dispatch => {
    const res = await profileAPI.updateStatus(status)
    if (res.data.resultCode === 0) {
        dispatch(setStatus(status))
    }
}

export default profileReducer